import React, { Component } from 'react'

export class WinPanel extends Component {
    render() {
        const { show, flip, restart } = this.props;
        if (show === 8)
        return (
            <div className="win">
                <h1>You Win!</h1>
                <h2>{flip} flips</h2>
                <button onClick={restart} className="restart">
                    New game
                </button>
            </div>
        )
        return null
    }
}

export default WinPanel
